<template lang="html">
  <b-container fluid class="top-50">
    <b-row>
      <b-col xl="3">
        <div class="card mine">
          <div class="card-header">
          </div>
          <div class="card-body">
            <b-img rounded="circle" src="https://avatars0.githubusercontent.com/u/13160193?s=460&v=4" alt="img" />
            <h6>{{yourName}}</h6>
            <span>{{sign}}</span>
          </div>
        </div>

        <div class="card contact">
          <div class="card-body">
            <h5>Contact</h5>
            <ul>
              <li>Went to Oh, Canada</li>
              <li>Became friends with Obama</li>
              <li>Worked at Github</li>
              <li>Lives in San Francisco, CA</li>
              <li>From Seattle, WA</li>
            </ul>
          </div>
        </div>

        <div class="card about">
          <div class="card-body">
            <h5>About</h5>
            <span>Etiam porta sem malesuada magna mollis euismod. Cras mattis consectetur purus sit amet fermentum. Aenean lacinia bibendum nulla sed consectetur.</span>
          </div>
        </div>
      </b-col>
      <b-col xl="9">
        <router-view></router-view>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: 'blog-work',
    computed: {
      ...mapState(['yourName', 'sign', 'addr', 'email', 'company', 'job', 'git', 'sina'])
    },
    methods: {
    },
    mounted () {
    }
  }
</script>

<style lang="less" scoped>
  .top-50 {
    margin-top: 30px;
  }

  .card {
    margin-bottom: 1.5rem;
  }

  .mine {
    text-align: center;

    .card-header {
      background: url('https://bootstrap-themes.github.io/application/assets/img/iceland.jpg');
      background-size: cover;
      min-height: 150px;
    }

    img {
      margin-top: -70px;
      margin-bottom: 10px;
      border: 3px solid #fff;
      max-width: 100px;
    }
  }
</style>




