<template>
  <ul class="list-unstyled">
    <b-media tag="li" class="mb-4" v-for="item in articleList" v-bind:key="item.title" v-on:click="goToArticle(item.title)">
      <b-img slot="aside" blank blank-color="#abc" width="64" alt="placeholder" />
      <h5 class="mt-0 mb-1">{{item.title}}</h5>
      {{item.desc}}
    </b-media>
  </ul>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'blog-articleList',
    computed: {
      ...mapState({
        articleList: state => state.article.articleList
      })
    },
    methods: {
      getArticleList () {
        this.$store.dispatch('getArticleList')
      },
      goToArticle (title) {
        this.$router.push({ name: 'Article', params: { title } })
      }
    },
    mounted () {
      this.getArticleList()
    }
  }
</script>

<style lang="less" scoped>

</style>



