import marked from 'marked'
import { APIMain, APIArticle } from '@/api/index'
import * as types from '@/store/mutation-types'

export default {
  state: {
    articleList: [],
    curArticle: {
      title: '',
      desc: '',
      time: '',
      tags: [],
      mdContent: ''
    }
  },
  getters: {
    ArticleContent (state) {
      return marked(state.curArticle.mdContent, { sanitize: true })
    }
  },
  actions: {
    getArticleList ({ commit }) {
      APIMain.getBaseData().then((data) => {
        const articleList = data.data.articleList
        commit(types.getArticleListSuccess, articleList)
      }, () => {
        commit(types.getArticleListError)
      })
    },
    getSingleArticle ({ dispatch, commit }, { title }) {
      dispatch('getArticleList').then(() => {
        APIArticle.getSingle({ title }).then((data) => {
          commit(types.getArticleSuccess, { title, mdContent: data.data })
        }, () => {
          commit(types.getArticleError)
        })
      }, () => {
        commit(types.getArticleError)
      })
    }
  },
  mutations: {
    [types.getArticleListSuccess] (state, articleList) {
      console.log(444)
      state.articleList = articleList
    },
    [types.getArticleListError] (state) {
    },
    [types.getArticleSuccess] (state, data) {
      for (let i = 0, len = state.articleList.length; i < len; i++) {
        const curItem = state.articleList[i]
        if (curItem.title === data.title) {
          state.curArticle.title = curItem.title
          state.curArticle.desc = curItem.desc
          state.curArticle.time = curItem.time
          state.curArticle.tags = curItem.tags
          state.curArticle.mdContent = data.mdContent
          break
        }
      }
    },
    [types.getArticleError] (state) {
    }
  }
}



