<template lang="html">
  <div class="card">
    <div class="card-body" v-html="ArticleContent"></div>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  export default {
    name: 'blog-article',
    computed: {
      ...mapGetters(['ArticleContent'])
    },
    methods: {
      getArticle ({ title }) {
        this.$store.dispatch('getSingleArticle', { title })
      }
    },
    mounted () {
      this.getArticle({ title: this.$route.params.title })
    }
  }
</script>

<style lang="less" scoped>
</style>




